.app__content {
    color: #fff;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

.app__content-main {
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
}

@media (max-width: 960px) {
    .app__content {
        flex-direction: column;
    }
    .app__content-main {
        padding: 15px;
    }
}

@media (max-width: 768px) {
    .app__content-main {
        padding: 15px 0;
    }
}