* {
    transition: 150ms ease-in-out all;
    scroll-behavior: smooth;
}

body {
    margin: 0;
    padding: 0;
    background-color: #eaeaea;
    scroll-behavior: smooth;
}

ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: #001a62;
    width: 100%;
}

div {
    box-sizing: border-box;
}

a:hover {
    text-decoration: none;
    color: #173fad;
    transition: 0ms all
}

h1 h2 h3 h4 h5 h6 {
    margin-top: 0;
    line-height: 1.1;
}

button {
    min-width: 32px;
}

.initialization {
    background-color: #eaeaea;
    width: 100%;
    height: 100vh;
}

.MuiSvgIcon-root {
    color: #222222;
}

::-webkit-scrollbar-button {
    width: 10px;
    height: 10px;
}

::-webkit-scrollbar-track {
    background-color: transparent;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #b7b6b6;
}

::-webkit-scrollbar {
    width: 8px;
}